import * as api from "../api/AuthApi";
import { useQuery, useMutation } from "react-query";
import { toast } from "react-toastify";
import { useAuth } from "../hooks/AuthContext";
import { useNavigate, useLocation } from "react-router-dom";

const useUser = () => {
    return useQuery("users", api.getUser);
};

// 
const useRegister = () => {
    const navigate = useNavigate();

    const { setIsAuth } = useAuth();

    return useMutation(api.register, {
        onSuccess: (data) => {

            console.log(data);
            toast.success("!", {
                position: toast.POSITION.TOP_CENTER,
                autoClose: 3000,
            });

            setIsAuth(true);
            navigate("/index");
        },
    });
};

// 
const useLogin = () => {
    const navigate = useNavigate();
    const location = useLocation();

    const { setIsAuth } = useAuth();

    const { from } = location.state as { from: string} || {from: { pathname: '/' }};

    return useMutation(api.login, {
        onSuccess: (data) => {
            console.log(data);
            toast.success(data.message, {
                position: toast.POSITION.TOP_CENTER,
                autoClose: 3000,
            });
            setIsAuth(true);
            navigate(from, { replace: true });
        },
    });
};

// 
const useLogOut = () => {
    const navigate = useNavigate();
    const { setIsAuth, setIsLoading } = useAuth();

    return useMutation(api.logout, {
        onSuccess: () => {
            toast.success("!", {
                position: toast.POSITION.TOP_CENTER,
                autoClose: 3000,
            });
            navigate("/login");
            setIsAuth(false);
            setIsLoading(false);
        },
        onError: () => {
            setIsLoading(false);
            toast.error("", {
                position: toast.POSITION.TOP_CENTER,
                autoClose: 3000,
            });
        },
    });
};

const useForgotPassword = () => {
    return useMutation(api.forgotPassword, {
        onSuccess: (data) => {

            console.log(data);
            toast.success("''", {
                position: toast.POSITION.TOP_CENTER,
                autoClose: 3000,
            });

        },
    });

}

export { useUser, useRegister ,useLogin, useLogOut, useForgotPassword };
